.container {
    // display: inline-block;\
    // width: calc(25% - 30px);
    // min-width: 200px;

    .info {
        display: flex;
        justify-content: space-between;
    }

    .imageWrapper {
        position: relative;
        .image {
            object-fit: cover;
            width: 100%;
            height: 300px;
        }

        &:hover {
            .btn {
                opacity: 1;
            }
        }
        .btn {
            &:focus,
            &:active {
                outline: 0;
            }

            &:hover {
                background-color: rgba(0, 0, 0, 0.8);
                color: #fff;
            }

            &:active {
                background-color: rgba(0, 0, 0, 0.95);
            }

            cursor: pointer;
            opacity: 0;
            transition: 0.15s;
            text-transform: uppercase;
            position: absolute;
            bottom: 30px;
            left: 50%;
            transform: translateX(-50%);
            padding: 15px 35px;
            background-color: rgba(255, 255, 255, 0.8);
            border: 1px solid #000;
        }
    }
}
